$color-primary: #107895;
.corner-img {
  height: 25rem;
  left: 2rem;
  position: absolute;
  top: 2rem;
  width: 25rem;
}
.signup {
  align-items: center;
  display: flex;
  flex-direction: column;
  height: 100%;
  justify-content: center;
  left: 0;
  position: absolute;
  top: 10rem;
  width: 100%;
}
.signup-input {
  background-color: #fff;
  border: solid 1px;
  border-radius: 0.4rem;
  font-size: 2rem;
  height: 5rem;
  margin-bottom: 1.5rem;
  margin-top: 1.5rem;
  outline: none;
  padding: 1rem;
  width: 30rem;
  &:focus {
    background-color: lighten($color-primary, 30);
  }
}
.signup-button {
  background-color: $color-primary;
  border: none;
  border-radius: 0.4rem;
  box-shadow: 0 1.4rem 2.8rem rgba(0, 0, 0, 0.25),
    0 0.5rem 0.5rem rgba(0, 0, 0, 0.22);
  color: whitesmoke;
  font-size: 2rem;
  height: 4rem;
  margin: 1.5rem;
  outline: none;
  width: 15rem;
  &:hover {
    background-color: darken($color-primary, 10);
    cursor: pointer;
  }
  &:active {
    box-shadow: 0 1.4rem 2.8rem rgba(0, 0, 0, 0.25),
      0 0.2rem 0.2rem rgba(0, 0, 0, 0.22);
    transform: translateY(-0.2rem);
  }
  &:disabled {
    background-color: rgb(177, 174, 174);
    cursor: no-drop;
  }
}
.signup-to-login {
  color: $color-primary;
  text-decoration: none;
}
.signup-input-error {
  color: red;
}
.signup-error {
  color: red;
}
.signup-success {
  align-items: center;
  display: flex;
  flex-direction: column;
  font-size: 3rem;
  height: 100%;
  justify-content: center;
  left: 0;
  position: absolute;
  top: 0;
  width: 100%;
  a {
    color: $color-primary;
    text-decoration: none;
  }
}
@media (max-width: 1024px) {
  .signup {
    top: 0;
  }
}
@media (max-width: 600px) {
  .signup-img {
    height: 15rem;
    width: 15rem;
  }
}
