.nav-menu {
  border-radius: 1rem;
  display: flex;
  flex-direction: row;
  justify-content: center;
  top: 2rem;
}
.nav-menu-item {
  background-color: $color-primary;
  color: white;
  font-size: 2.5rem;
  outline: none;
  padding: 2rem;
  position: relative;
  text-align: center;
  text-decoration: none;
  width: 15rem;
  z-index: 50;
  &:hover {
    cursor: pointer;
    font-weight: bold;
  }
}
.no-decoration {
  color: white;
  text-decoration: none;
  z-index: 0;
}
.active-nav {
  font-weight: bold;
  text-decoration: none;
}
.nav-menu-last-item {
  border-right: none;
}
.categories-list {
  background-color: white;
  border: 0.2rem solid $color-primary;
  display: flex;
  flex-direction: column;
  font-size: 1.7rem;
  left: -5rem;
  list-style-type: none;
  opacity: 0;
  position: absolute;
  text-align: left;
  text-decoration: none;
  top: 8rem;
  transition: all 0.5s;
  visibility: hidden;
  width: 25rem;
  writing-mode: horizontal-tb;
}
.categories {
  &:hover {
    .categories-list {
      opacity: 1;
      visibility: visible;
    }
  }
}
.categories-list-item {
  color: black;
  padding: 1rem;
  text-decoration: none;
  &:hover {
    background-color: lighten($color-primary, 10);
  }
}
.last {
  border-bottom: none;
}
@media (max-width: 1024px) {
  .navigation {
    display: none;
  }
}
@media (max-height: 600px) {
  .logo {
    display: none;
  }
  .nav-menu-item {
    font-size: 1.6rem;
  }
}
