$color-primary: #107895;
.footer {
  background-color: white;
  padding: 2rem;
  position: relative;
  width: 100%;
  height: 18rem;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
}
.footer-logo {
  height: 12rem;
  width: 12rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.footer-logo-img {
  height: 12rem;
  width: 12rem;
}
.footer-about {
  display: flex;
  flex-direction: column;
  height: 14rem;
  max-width: 60%;
}
.footer-about-title {
  font-size: 2rem;
}
.footer-location {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 14rem;
  max-width: 22%;
}
.footer-location-title {
  font-size: 2rem;
  text-align: end;
}
.footer-location-text {
  font-size: 1.4rem;
  text-align: end;
}
.footer-pin {
  color: $color-primary;
}
.about-title {
  font-size: 2rem;
}
.about-text {
  font-size: 1.4rem;
}
@media (max-width: 1024px) {
  .footer {
    padding: 2rem;

    margin-bottom: 12rem;
  }
}
@media (max-width: 600px) {
  .footer {
    padding: 2rem;
  }
  .footer-logo {
    display: none;
  }
  .footer-about {
    width: 65%;
  }
  .footer-location {
    width: 35%;
  }
  .footer-location-text {
    font-size: 1.4rem;
  }
}
